import {
  Scene,
  PerspectiveCamera,
  WebGLRenderer,
  WebGLCubeRenderTarget,
  HalfFloatType,
  CubeCamera
} from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import Stats from 'three/examples/jsm/libs/stats.module.js'
const baseUrl = process.env.BASE_URL
export default class ThreeScene {
  public scene: any
  public camera: any
  private renderer: any
  private controls: any
  private container: any
  private stats: any
  public tweens: any = []
  public cubeRenderTarget: any
  private cubeCamera: any
  constructor (elId: string) {
    this.scene = new Scene()
    this.camera = new PerspectiveCamera(60, window.innerWidth / window.innerHeight, 0.1, 2000)
    this.renderer = new WebGLRenderer({
      antialias: true, // 抗锯齿
      logarithmicDepthBuffer: true, // 解决重叠的问题
      alpha: true
    })
    this.renderer.setSize(window.innerWidth, window.innerHeight)
    this.renderer.setPixelRatio(window.devicePixelRatio) // 优化性能
    this.renderer.shadowMap.enabled = true
    this.controls = new OrbitControls(this.camera, this.renderer.domElement)
    this.controls.maxDistance = 13.4
    this.controls.minDistance = 8
    this.controls.autoRotate = false
    this.controls.autoRotateSpeed = 0.5
    this.controls.enableDamping = true // 添加阻尼效果使拖动的时候更加圆滑
    this.controls.dampingFactor = 0.05 // 默认0.05,数值越小惯性越大
    // 角度
    this.controls.minPolarAngle = 1.1
    this.controls.maxPolarAngle = 1.5
    this.controls.enablePan = false
    this.container = document.getElementById(elId)
    this.container.appendChild(this.renderer.domElement)
    window.addEventListener('resize', () => {
      try {
        this.camera.aspect = window.innerWidth / window.innerHeight
        this.camera.updateProjectionMatrix()
        this.renderer.setSize(window.innerWidth, window.innerHeight)
      } catch (error) {
        console.error('Resize event error:', error)
      }
    })
    // this.stats = new Stats()
    // document.body.appendChild(this.stats.domElement)
    // 背景颜色
    this.renderer.setClearColor(0x000000, 1)
    // 立体相机
    this.cubeRenderTarget = new WebGLCubeRenderTarget(256)
    this.cubeRenderTarget.texture.type = HalfFloatType
    this.cubeCamera = new CubeCamera(1, 1000, this.cubeRenderTarget)
    // 相机 6、4、-20
    const target = { x: 16, y: 7, z: 0.563 }
    this.camera.position.set(target.x, target.y, target.z)
    this.camera.rotation.set(0, 90, 90)
  }

  public animate (time?: number) {
    this.controls.update()
    // this.stats.update()
    this.renderer.render(this.scene, this.camera)
    if (this.tweens) {
      this.tweens.forEach((tween: any) => {
        tween.update(time)
      })
    }
    this.cubeCamera.update(this.renderer, this.scene)

    requestAnimationFrame(this.animate.bind(this))
  }
}
