import {
  Mesh,
  Color,
  FrontSide,
  DoubleSide,
  RectAreaLight,
  PlaneGeometry,
  TextureLoader,
  SRGBColorSpace,
  SphereGeometry,
  DirectionalLight,
  MeshBasicMaterial,
  MeshStandardMaterial
} from 'three'
import { RectAreaLightHelper } from 'three/examples/jsm/helpers/RectAreaLightHelper.js'
import { RectAreaLightUniformsLib } from 'three/examples/jsm/lights/RectAreaLightUniformsLib.js'
import GUI from 'lil-gui'
const baseUrl = process.env.BASE_URL
// 加载纹理文件
const textureLoader = new TextureLoader()
const texture = textureLoader.load(baseUrl + 'img/floor_roughness.webp')
const shpereBg = textureLoader.load(baseUrl + 'img/mg6-front_nor.jpg')
// 平面光源
const rectLight = new RectAreaLight(new Color('rgb(173, 255, 0)'), 4, 6, 16) // 颜色，强度，宽度，高度
RectAreaLightUniformsLib.init()
rectLight.position.y = 6
const helper = new RectAreaLightHelper(rectLight)
rectLight.lookAt(0, 0, 0)
// 地板
const floorGeometry = new PlaneGeometry(64, 128)
const floorMaterial = new MeshStandardMaterial({
  map: texture,
  roughness: 0.1,
  metalness: 0.4,
  side: FrontSide
})
const floor = new Mesh(floorGeometry, floorMaterial)
floor.receiveShadow = true
floor.rotateX(-Math.PI / 2)
// 平行光
const ambientLight = new DirectionalLight(0xffffff, 0)
ambientLight.position.set(0, 10, 0)
ambientLight.intensity = 0
// 球体
const sphereGeometry = new SphereGeometry(12, 32, 32)
shpereBg.colorSpace = SRGBColorSpace
sphereGeometry.scale(-1, 1, 1)
const sphereMaterial = new MeshBasicMaterial({
  map: shpereBg,
  side: DoubleSide
})
const innerSphere = new Mesh(sphereGeometry, sphereMaterial)
innerSphere.visible = false
export {
  rectLight,
  helper,
  floor,
  ambientLight,
  innerSphere
}
