<template>
  <div>
    <div id="scene-container"
      @mousedown.stop.prevent="enterPress"
      @touchstart.stop.prevent="enterPress"
      @touchend.stop.prevent="upPress"
      @mouseup.stop.prevent="upPress"></div>
    <div class="color-wrap">
      <div class="text-bg">
        <div @click="checkView('main')" :class="['text-btn', viewStatus === 'main' ? 'active': '']">总览</div>
        <div @click="checkView('inside')" :class="['text-btn', viewStatus === 'inside' ? 'active': '']">车内</div>
      </div>
      <div class="bg">
        <div v-for="item of colors" :data-color="item" :key="item" @click.stop="carColorChange(item)" class="color-btn"></div>
      </div>
    </div>
    <div class="mask" v-if="maskVisible">
      <div class="progress">
        <div class="sel-progress" :style="{ width: progressRatio + '%' }">{{ progressRatio }}%</div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { onMounted, ref, Component } from 'vue'
import { Color, MeshBasicMaterial, Vector3, ShaderMaterial, DoubleSide, Clock, MathUtils } from 'three'
import { MeshoptDecoder } from 'three/examples/jsm/libs/meshopt_decoder.module.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { createNoise2D } from 'simplex-noise'
import ThreeScene from '@/utils/three/InitScene'
import GUI from 'lil-gui'
import {
  rectLight,
  floor,
  helper,
  ambientLight,
  innerSphere
} from '@/utils/three/BasicMesh'
import {
  tireMaterial,
  bodyMaterial,
  glassMaterial,
  carLightMaterial
} from '@/utils/three/BasicMaterial'
import TWEEN, { Tween } from '@tweenjs/tween.js'
import { vertexShader, fragmentShader } from '@/shaders/cylinderShader'
const baseUrl = process.env.BASE_URL
const loader = new GLTFLoader()
let Scene: any
let model: any
let cylinderModel: any
let cylinderMaterial: any
const viewStatus: any = ref('main')
const doorRf: any[] = []
const tires: any[] = []
let isMouseup = true
const progressRatio = ref(0)
const maskVisible = ref(false)
const tweenGroupA: any = new TWEEN.Group()
const tweenGroupB: any = new TWEEN.Group()
const tweenGroupC: any = new TWEEN.Group()
const tweenGroupD: any = new TWEEN.Group()
const noise2d = createNoise2D()
const tweenedPosOffset = new Vector3(0, 0, 0)
const colors: any = [
  '0x2eafc1',
  '0xffffff',
  '0xffff00',
  '0xff0000'
]
onMounted(async () => {
  // 初始化场景
  Scene = await new ThreeScene('scene-container')

  // 开始动画
  Scene.animate()

  // 添加汽车模型
  addCar()
  // const obj = {
  //   cb: () => {
  //     console.log(Scene.camera)
  //   }
  // }
  // const gui = new GUI()
  // gui.add(obj, 'cb').name('获取视角')
  cylinderMaterial = new ShaderMaterial({
    uniforms: {
      iTime: {
        value: 0
      },
      iSpeedFactor: {
        value: 0
      }
    },
    transparent: true,
    side: DoubleSide,
    vertexShader,
    fragmentShader
  })
  loader.setMeshoptDecoder(MeshoptDecoder)
  const path = baseUrl + 'glb/sm_speedup/sm_speedup.gltf'
  loader.load(path, (gltf: any) => {
    cylinderModel = gltf.scene
    cylinderModel.traverse((child: any) => {
      if (child.isMesh) {
        child.material = cylinderMaterial
      }
    })
    cylinderModel.rotation.y = Math.PI / 2
    cylinderModel.scale.set(3, 3, 3)
    Scene.scene.add(cylinderModel)
  })
  Scene.scene.add(ambientLight)
  Scene.scene.add(innerSphere)
  Scene.tweens.push(tweenGroupA, tweenGroupB, tweenGroupC, tweenGroupD)
})
// 天花板
const addCeiling = () => {
  // 添加天花板
  Scene.scene.add(rectLight)
  Scene.scene.add(helper)
  new TWEEN.Tween({ x: 173, y: 255, z: 0 }, tweenGroupC)
    .to({ x: 255, y: 255, z: 255 }, 2500)
    .easing(TWEEN.Easing.Quadratic.InOut)
    .onUpdate((that: any) => {
      const { x, y, z } = that
      rectLight.color = new Color(`rgb(${Math.ceil(x)}, ${Math.ceil(y)}, ${Math.ceil(z)})`)
    }).start()
  new TWEEN.Tween({ x: 16, y: 7, z: 0.563 }, tweenGroupC)
    .to({ x: 14.65, y: 3.15, z: 0.563 }, 2000)
    .easing(TWEEN.Easing.Quadratic.InOut)
    .onUpdate((that: any) => {
      Scene.camera.position.set(that.x, that.y, that.z)
    })
    .start()
  new TWEEN.Tween({ z: 4 }, tweenGroupC)
    .to({ z: 0 }, 2000)
    .easing(TWEEN.Easing.Quadratic.Out)
    .onUpdate((that: any) => {
      model.position.z = that.z
    })
    .start()
  tires.forEach((tireItem: {rotation: { x: number }}) => {
    new TWEEN.Tween({ x: 0 }, tweenGroupC)
      .to({ x: -Math.PI * 2 }, 2000)
      .easing(TWEEN.Easing.Quadratic.Out)
      .onUpdate((that: { x: number }) => {
        tireItem.rotation.x = that.x
      })
      .start()
  })
}
const addFloor = () => {
  Scene.scene.add(floor)
}
const addCar = () => {
  tireMaterial.envMap = Scene.cubeRenderTarget.texture
  bodyMaterial.envMap = Scene.cubeRenderTarget.texture
  glassMaterial.envMap = Scene.cubeRenderTarget.texture
  const modelPath = baseUrl + 'glb/car/scene.gltf'
  loader.load(modelPath, (gltf: any) => {
    model = gltf.scene
    model.scale.set(0.02, 0.02, 0.02)
    model.position.y = 1.6
    model.position.z = 4
    // Car_body
    const body = [
      'door_lf_movsteer_101_0',
      'door_rf_movsteer_101_0',
      'door_lr_movsteer_101_0',
      'door_rr_movsteer_101_0',
      'platnomor_hitam0_0',
      'cahrome_movsteer_101_0',
      'front_black_front_black0_0',
      'Seat_Leather_white_Seat_Leather_white0_0',
      'door_lf_mirror_inside0_0',
      'door_rf_mirror_inside0_0',
      'hub_rf_hub_rf0_0',
      'hub_rf_hub_rf1_0',
      'hub_rb_hub_rb0_0',
      'hub_lb_hub_rb0_0',
      'hub_lf_hub_rf0_0',
      'hub_lf_hub_rf1_0'
    ]
    model.traverse((child: any) => {
      if (child.name.indexOf('wheels') !== -1 || child.name.indexOf('wheels001') !== -1) {
        // 车胎
        if (child.name === 'wheels' || child.name === 'wheels001') {
          tires.push(child)
        }
        if (child.name === 'wheels_wheels6_0' || child.name === 'wheels001_wheels6_0') {
          // 轮毂
          child.material = tireMaterial
        }
        if (child.name === 'wheels_wheels3_0' || child.name === 'wheels001_wheels3_0') {
          child.material = new MeshBasicMaterial({
            color: 0x000000
          })
        }
      } else if (child.name === 'chrome_bonnet_ok_movsteer_101_0' || child.name === 'chrome001_movsteer_101_0') {
        // 车标
        child.material = tireMaterial
      } else if (child.name.indexOf('glass') !== -1) {
        // 玻璃
        child.material = glassMaterial
      } else if (child.name === 'door_rf' || child.name === 'door_rr') {
        // 左边两车门
        doorRf.push(child)
      } else if (child.name === 'bonnet_dummy') {
        // 车前盖
      } else if (child.name === 'boot_dummy' || child.name === 'JUST_BLACK001') {
        // 后备箱
      } else if (child.name === 'movsteer_10_movsteer_100_0') {
        // 方向盘
      } else if (child.name === 'tembus_belakang_tembus_red0_0' || child.name === 'tembus_depan_ok_tembus_red0_0') {
        // 车灯
        child.material = carLightMaterial
      } else if (child.name === 'Seat_Leather_white_Seat_Leather_white0_0') {
        // 车椅
      } else if (body.includes(child.name)) {
        // 过滤
      } else {
        child.material = bodyMaterial
      }
      // child.castShadow = true
    })
    Scene.scene.add(model)
    maskVisible.value = false
    // 天花板
    addCeiling()
    // 地板
    addFloor()
  }, (xhr: any) => {
    maskVisible.value = true
    progressRatio.value = xhr.total > 0 ? Math.min(xhr.loaded / xhr.total * 100, 100) : 0
  })
}
const tunelAnimationStart = () => {
  if (isMouseup) return
  const startValues = {
    iTime: 0,
    iSpeedFactor: 0
  }
  const endValues = { iTime: 1000, iSpeedFactor: 2 }
  new TWEEN.Tween(startValues, tweenGroupB)
    .to(endValues, 3000)
    .easing(TWEEN.Easing.Linear.None)
    .onUpdate((that) => {
      cylinderMaterial.uniforms.iTime.value = (that.iTime / 400).toFixed(2)
      cylinderMaterial.uniforms.iSpeedFactor.value = (that.iSpeedFactor).toFixed(2)
    })
    .onComplete(() => {
      tweenGroupB.removeAll()
      tunelAnimationStart()
    })
    .start()
}
const tunelAnimationStop = () => {
  const startValues = {
    iTime: cylinderMaterial.uniforms.iTime.value,
    iSpeedFactor: cylinderMaterial.uniforms.iSpeedFactor.value
  }
  const endValues = { iTime: 0, iSpeedFactor: 0 }
  new TWEEN.Tween(startValues, tweenGroupA)
    .to(endValues, 1500)
    .easing(TWEEN.Easing.Quadratic.Out)
    .onUpdate((that: any) => {
      cylinderMaterial.uniforms.iSpeedFactor.value = that.iTime
      cylinderMaterial.uniforms.iSpeedFactor.value = that.iSpeedFactor
    })
    .start()
}
const fbm = ({
  octave = 3,
  frequency = 1,
  amplitude = 0.5,
  lacunarity = 2,
  persistance = 0.5
} = {}) => {
  let value = 0
  for (let i = 0; i < octave; i++) {
    const noiseValue = noise2d(frequency, frequency)
    value += noiseValue * amplitude
    frequency *= lacunarity
    amplitude *= persistance
  }
  return value
}
// 长按
const enterPress = () => {
  if (viewStatus.value === 'inside') return
  isMouseup = false
  rectLight.visible = false
  floor.visible = false
  helper.visible = false
  ambientLight.intensity = 1
  // 镜头晃动
  const shake = () => {
    const t = new Clock().elapsedTime
    const posOffset = new Vector3(
      fbm({
        frequency: t * 0.5 + MathUtils.randFloat(1000, 0),
        amplitude: 0.1
      }),
      fbm({
        frequency: t * 0.5 + MathUtils.randFloat(1000, 0),
        amplitude: 0.1
      }),
      fbm({
        frequency: t * 0.5 + MathUtils.randFloat(1000, 0),
        amplitude: 0.1
      })
    )
    posOffset.multiplyScalar(0.1 * 1)
    new TWEEN.Tween(tweenedPosOffset, tweenGroupD)
      .to({
        x: posOffset.x,
        y: posOffset.y,
        z: posOffset.z
      }, 600) // 减少持续时间
      .easing(TWEEN.Easing.Quadratic.InOut)
      .onUpdate(() => {
        // 更新你的位置或其他逻辑
        Scene.camera.position.add(tweenedPosOffset)
      })
      .start()
      .onComplete(() => {
        tweenGroupD.removeAll()
        shake()
      })
  }
  shake()
  const tiresLoop = () => {
    if (isMouseup) return
    tires.forEach((tireItem: {rotation: { x: number }}) => {
      new TWEEN.Tween({ x: 0 }, tweenGroupA)
        .to({ x: -Math.PI * 2 }, 500)
        .easing(TWEEN.Easing.Linear.None)
        .onUpdate((that: { x: number }) => {
          tireItem.rotation.x = that.x
        })
        .start()
        .onComplete(() => {
          tweenGroupA.removeAll()
          tiresLoop()
        })
    })
  }
  tires.forEach((tireItem: {rotation: { x: number }}) => {
    new TWEEN.Tween({ x: 0 }, tweenGroupA)
      .to({ x: -Math.PI * 2 }, 1500)
      .easing(TWEEN.Easing.Quadratic.In)
      .onUpdate((that: { x: number }) => {
        tireItem.rotation.x = that.x
      })
      .start()
      .onComplete(() => {
        tiresLoop()
      })
  })
  new TWEEN.Tween({ zoom: Scene.camera.fov }, tweenGroupA)
    .to({ zoom: 50 }, 3000)
    .easing(TWEEN.Easing.Quadratic.Out)
    .onUpdate((that: any) => {
      Scene.camera.fov = that.zoom
      Scene.camera.updateProjectionMatrix()
    }).start()
  new TWEEN.Tween({ x: 0, y: 0, z: 0 }, tweenGroupA)
    .to({ x: 255, y: 255, z: 255 }, 600)
    .easing(TWEEN.Easing.Quadratic.InOut)
    .onUpdate((that: any) => {
      const { x, y, z } = that
      carLightMaterial.emissive = new Color(`rgb(${Math.ceil(x)}, ${Math.ceil(y)}, ${Math.ceil(z)})`)
    }).start()
  tunelAnimationStart()
}
// 松开
const upPress = () => {
  if (viewStatus.value === 'inside') return
  isMouseup = true
  tweenGroupA.removeAll()
  tweenGroupB.removeAll()
  tweenGroupC.removeAll()
  tweenGroupD.removeAll()
  new TWEEN.Tween({ zoom: Scene.camera.fov }, tweenGroupA)
    .to({ zoom: 60 }, 2000)
    .easing(TWEEN.Easing.Quadratic.Out)
    .onUpdate((that: any) => {
      Scene.camera.fov = that.zoom
      Scene.camera.updateProjectionMatrix()
    })
    .start()
  tires.forEach((tireItem: {rotation: { x: number }}) => {
    new TWEEN.Tween({ x: 0 }, tweenGroupA)
      .to({ x: -Math.PI * 2 }, 1000)
      .easing(TWEEN.Easing.Quadratic.Out)
      .onUpdate((that: { x: number }) => {
        tireItem.rotation.x = that.x
      })
      .start()
  })
  new TWEEN.Tween({ x: 255, y: 255, z: 255 }, tweenGroupA)
    .to({ x: 0, y: 0, z: 0 }, 1000)
    .easing(TWEEN.Easing.Quadratic.InOut)
    .onUpdate((that: any) => {
      const { x, y, z } = that
      carLightMaterial.emissive = new Color(`rgb(${Math.ceil(x)}, ${Math.ceil(y)}, ${Math.ceil(z)})`)
    }).start()
    .onComplete(() => {
      if (!isMouseup) return
      rectLight.visible = true
      floor.visible = true
      helper.visible = true
      ambientLight.intensity = 0
    })
  tunelAnimationStop()
}
const carColorChange = (color: string) => {
  bodyMaterial.color.setHex(color)
}
const checkView = (type: string) => {
  const { x, y, z } = Scene.camera.position
  const cleatTween = () => {
    tweenGroupA.removeAll()
  }
  cleatTween()
  if (type === 'main') {
    rectLight.visible = true
    floor.visible = true
    helper.visible = true
    ambientLight.visible = true
    innerSphere.visible = false
    model.visible = true
    Scene.controls.autoRotate = false
    Scene.controls.maxDistance = 13.4
    Scene.controls.minDistance = 8
    Scene.controls.minPolarAngle = 1.1
    Scene.controls.maxPolarAngle = 1.5
    new TWEEN.Tween({ x, y, z }, tweenGroupA)
      .to({ x: 16, y: 7, z: 0.563 }, 2000)
      .easing(TWEEN.Easing.Quadratic.InOut)
      .onUpdate((that: any) => {
        Scene.camera.position.set(that.x, that.y, that.z)
      })
      .start()
      .onComplete(() => {
        cleatTween()
      })
  } else {
    rectLight.visible = false
    floor.visible = false
    helper.visible = false
    ambientLight.visible = false
    Scene.controls.autoRotate = false
    innerSphere.visible = true
    model.visible = false
    // 缩放比例控制
    Scene.controls.maxDistance = 6
    Scene.controls.minDistance = 2
    // 角度
    Scene.controls.minPolarAngle = 0
    Scene.controls.maxPolarAngle = 4
    new TWEEN.Tween({ x, y, z }, tweenGroupA)
      .to({ x: 2.681, y: 0.724, z: 0.104 }, 2000)
      .easing(TWEEN.Easing.Quadratic.InOut)
      .onUpdate((that: any) => {
        Scene.camera.position.set(that.x, that.y, that.z)
      })
      .start()
      .onComplete(() => {
        cleatTween()
      })
  }
  viewStatus.value = type
}
</script>

<style lang="less" scoped>
  #scene-container {
    width: 100%;
    height: 100%;
  }

  .color-wrap {
    position: absolute;
    bottom: 20px;
    display: flex;
    width: 100%;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    .bg {
      display: flex;
      justify-content: center;
      padding: 4px 20px;
      background: rgba(109, 109, 109, 0.4);
      border-radius: 50px;
    }

    .color-btn {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      border: 4px solid rgba(63, 63, 63, 0.44);
      margin: 10px;
      cursor: pointer;

      &:nth-child(1) {
        background-color: rgb(46, 175, 193);
      }

      &:nth-child(2) {
        background-color: #fff;
      }

      &:nth-child(3) {
        background-color: rgb(251, 251, 108);
      }

      &:nth-child(4) {
        background-color: rgb(253, 70, 70);
      }
    }
  }

  .text-bg {
    display: flex;
  }

  .text-btn {
    margin: 20px;
    cursor: pointer;
    color: #fff;
    padding: 0 4px;
    text-shadow: -1px -1px #000000;
    &.active {
      color: #cafe2d;
      position: relative;

      &::before {
        content: ' ';
        width: 100%;
        height: 4px;
        position: absolute;
        background: #cafe2d;
        bottom: -8px;
        border-radius: 50px;
        left: 0;
      }
    }
  }
  .mask {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    .progress {
      width: 80vw;
      height: 1.6vh;
      background: rgba(188, 188, 188, 1);
      border-radius: 50px;
      position: relative;
      line-height: 1.6vh;
      .sel-progress {
        width: 70%;
        height: 100%;
        background: #cafe2d;
        border-radius: 50px;
        position: absolute;
        left: 0;
        color: #fff;
        text-align: end;
        text-indent: 101%;
      }
    }
  }
</style>
