import {
  DoubleSide,
  MeshStandardMaterial,
  MeshPhysicalMaterial
} from 'three'

const tireMaterial = new MeshStandardMaterial({
  color: 0xffffff,
  metalness: 0.8,
  side: DoubleSide,
  roughness: 0.0,
  envMapIntensity: 0.8
})

const bodyMaterial = new MeshPhysicalMaterial({
  color: 0x2eafc1,
  envMapIntensity: 0.8,
  clearcoat: 1.0,
  clearcoatRoughness: 0.03,
  side: DoubleSide,
  roughness: 0,
  metalness: 0.1
})
const glassMaterial = new MeshStandardMaterial({
  color: 0x000000,
  transparent: true,
  envMapIntensity: 0.40,
  opacity: 0.84,
  roughness: 0.0,
  metalness: 0.0
})
const carLightMaterial = new MeshStandardMaterial({
  color: 0x000000,
  transparent: true,
  opacity: 0.84,
  roughness: 0.0,
  metalness: 0.0
})
export {
  tireMaterial,
  bodyMaterial,
  glassMaterial,
  carLightMaterial
}
